import service from '../../service.js';
import cityData from '@/components/citydata.js';
export default {
	data() {
		return {
			email:'',
			emailCode:'',
			password:'',
			password2:'',
			tabIndex: 0,
			navList: [
				{
					text: '手机号注册',
					state: '0'
				},
				{
					text: '邮箱号注册',
					state: '1'
				}
			],
			show: 0,
			titlt: '',
			subtitle: '',
			countdown: 62,
			checked: true,
			account: '',
			nickname: '',
			pwd: '',
			pwd2: '',
			verifycode: '',
			show_id: '',
			invite_code: '',
			index: 0, // 选择城市第一次点击省份的索引
			index1: 0,
			city: '', // 省份
			city1: '', // 城市
			allcity: '', //省份城市
			// aggregate:[],
			citys: [],
			citys1: [],
			// oldCityList:[],
			// address:[]
			url: '', //外部链接
			value: [],
			indicatorstyle: 'border-top:4upx solid #DADADA;border-bottom:4upx solid #DADADA;height:100upx;text-align:center;',
			showwind: 0,
			showtext: 0,
			richt: '', //用户协议
			throttle: true //防止用户多次点击
		};
	},
	methods: {
		//swiper 切换
		changeTab(e) {
			this.tabClick(e.target.current);
		},

		//顶部tab点击
		tabClick(index) {
			this.change(index);
		},
		change(index) {
			this.type = this.navList[index].type;
			this.tabIndex = index;
		},
		showwinds() {
			this.showwind = 0;
			this.showtext = 0;
		},
		/*------城市选择-------*/
		select1() {
			//选择城市
			this.allcity = '';
			this.citys = cityData;
			console.log(this.citys);
			this.showwind = 1;
			this.city = ''; // 省份
			this.city1 = ''; // 城市
			// this.index = 0;
			// this.index1 = 0;
		},
		bindChange: function(e) {
			//prck-view自带点击事件 e会获得选中数组的下标
			this.index = e.detail.value[0];
			this.index1 = e.detail.value[1];
			this.citys1 = this.citys[this.index].childrens;
			console.log(e, this.index);
			this.city = this.citys[this.index].label;
			this.city1 = this.citys1[this.index1].label;
		},
		cancel() {
			//取消选择城市
			this.showwind = 0;
		},
		comsel() {
			//完成选择城市
			if (this.city == '') {
				this.city = this.citys[0].label;
			}
			if (this.city1 == '') {
				this.citys1 = this.citys[this.index].childrens;
				this.city1 = this.citys1[0].label;
			}
			this.allcity = this.city + ' ' + this.city1;
			console.log(this.allcity);
			this.showwind = 0;
		},
		/*------城市选择-------*/

		back() {
			uni.navigateTo({
				url: '/pages/login/login'
			});
		},
		jump(url) {
			uni.navigateTo({
				url: url
			});
		},
		// 跳转下载链接
		jum() {
			uni.navigateTo({
				url: '/pages/reg/download'
			});
		},
		checkeds() {
			this.checked = !this.checked;
			console.log(this.checked);
