		// },
		send() {
			var self = this;
			if (self.checked == false) {
				uni.showToast({
					icon: 'none',
					title: '未同意用户协议'
				});
				return;
			}
			uni.request({
				url: service.api.reg.code,
				method: 'GET',
				data: {
					mobile: this.account,
					temp: 'sms_reg'
				},
				success: res => {
					console.log(res);
					var res = res.data;
					if (res.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						self.codetime(self, 60);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						return;
					}
				}
			});
			// this.codetime(this,60);
		},
		emailCodes(){
			var self = this;
			if (self.checked == false) {
				uni.showToast({
					icon: 'none',
					title: '未同意用户协议'
				});
				return;
			}
			uni.request({
				url: service.api.reg.sendEmailCode,
				method: 'GET',
				data: {
					email: self.email,
				},
				success: res => {
					console.log(res);
					var res = res.data;
					if (res.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						self.codetime(self, 60);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						return;
					}
				}
			});
			},
		//手機驗證碼獲取倒計時
		codetime: function(obj, num) {
			if (num < 10) {
				obj.countdown = '0' + num;
			} else {
				obj.countdown = num;
			}
			num--;
			if (num < 0) {
				obj.countdown = 61;
			} else {
				setTimeout(function() {
					obj.codetime(obj, num);
				}, 1000);
			}
		},
		registered() {
			var self = this;
				if (!(/^1[3-9][0-9]\d{8,11}$/.test(self.account))) {
				uni.showToast({
					icon: 'none',
					title: '请输入11位手机号!'
				});
				return;
			}
			if (self.pwd.length < 8 && !(/(?=.*\d)(?=.*[A-z])^[0-9A-z]{8,}$/.test(self.pwd))) {
				uni.showToast({
					icon: 'none',
					title: '请输入数字与字母结合的八位数以上登录密码'
				});
				return;
			}
			if (self.pwd2.length !== 6) {
				uni.showToast({
					icon: 'none',
					title: '请输入六位数的置换密码'
				});
				return;
			}
			if (self.checked == false) {
				uni.showToast({
					icon: 'none',
					title: '未同意用户协议'
				});
				return;
			}
			if (this.city == '' || this.city1 == '') {
				uni.showToast({
					title: '请选择省份城市',
					icon: 'none'
				});
				return;
			}
			if (this.invite_code == '') {
				uni.showToast({
					title: '请输入邀请码',
					icon: 'none'
				});
				return;
			}
			uni.request({
				url: service.api.reg.reg,
				method: 'POST',
				data: {
					account: self.account,
					pwd: self.pwd,
					pwd2: self.pwd2,
					invite_code: self.invite_code,
					verifycode: self.verifycode,
					city: self.city1, // 归属城市
					province: self.city //省份
				},
				success(res) {
					console.log(res.data);
					if (res.data.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
						self.throttle = false;
						setTimeout(function(self, res) {











						}, 1500);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
						self.throttle = true;
					}
				}
			});
		},
		eamilRegistered(){
			var self = this;
			if (!(/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,5}$/.test(self.email))) {
				uni.showToast({
					icon: 'none',
					title: '请输入合理的邮箱号!'
				});
				return;
			}											 
			if (self.password.length < 8 && !(/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,}$/.test(self.password))) {
				uni.showToast({
					icon: 'none',
					title: '请输入数字与字母结合的八位数以上登录密码'
				});
				return;
			}

			if (self.password2.length < 6 && !(/^(?=.*?[0-9])(?=.*?[a-z])[0-9a-z]{6,}$/.test(self.password2))) {
				uni.showToast({
					icon: 'none',
					title: '请输入数字与字母结合的六位数以上置换密码'
				});
				return;
			}
			if (self.checked == false) {
				uni.showToast({
					icon: 'none',
					title: '未同意用户协议'
				});
				return;
			}
			if (this.city == '' || this.city1 == '') {
				uni.showToast({
					title: '请选择省份城市',
					icon: 'none'
				});
				return;
			}
			if (this.invite_code == '') {
				uni.showToast({
					title: '请输入邀请码',
					icon: 'none'
				});
				return;
			}
			uni.request({
				url: service.api.reg.emailRegister,
				method: 'POST',
				data: {
					email:self.email,
					verifycode:self.emailCode,
					pwd:self.password,
					pwd2:self.password2,
					invite_code: self.invite_code,
					city: self.city1, // 归属城市
					province: self.city ,//省份
				},
				success(res) {
					console.log(res.data);
					if (res.data.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
						self.throttle = false;
						setTimeout(function(self, res) {





			





						}, 1500);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
						self.throttle = true;
					}
				}
			});
		},
		readreg() {
			uni.request({
				url: service.api.reg.agree,
				success: res => {
					console.log(this);
					this.richt = res.data.data.registration_agreement;
				}
			});
			this.showtext = 2;
		}
	},
	onShow() {
		// 判断是在什么系统下跳转的
